.todoItem {
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    margin-left: 20px;
    margin-right: 20px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}
  
.todoItem-text {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.todoItem-text-complete {
    text-decoration: line-through;
}
  
.icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
}

.icon-check {
    position: absolute;
    left: 12px;
}

.icon-check:hover {
    color: #4caf50;
}

.icon-check-active {
    color: #4caf50;
}

.icon-delete {
    position: absolute;
    /* top: -24px; */
    right: 0;
}

.icon-delete:hover {
    color: red;
}